import Carousel from 'react-multi-carousel'
import 'react-multi-carousel/lib/styles.css';
import Countdown from 'react-countdown';
import { Box, Typography,Button, styled, Divider } from '@mui/material';
import { Link } from 'react-router-dom';


const responsive = {
 desktop: {
   breakpoint: { max: 3000, min: 1024 },
   items: 5,
   slidesToSlide: 3 // optional, default to 1.
 },
 tablet: {
   breakpoint: { max: 1024, min: 464 },
   items: 2,
   slidesToSlide: 2 // optional, default to 1.
 },
 mobile: {
   breakpoint: { max: 464, min: 0 },
   items: 1,
   slidesToSlide: 1 // optional, default to 1.
 }
};
const Component = styled(Box)`
margin-top:10px;
background:#ffffff;

`;
const Deal = styled(Box)`
padding:15px 20px;
display:flex;
`;
const Timer = styled(Box)`
margin-left: 10px ;
display:flex;
align-items:center;
color:#7f7f7f;
`;
const DealText = styled(Typography)`
font-size:22px;
font-weight:600;
margin-right:25px;
line-height:32px;
`;
const ViewAllButton = styled(Button)`
margin-left:auto;
background-color:#2874f0;
border-radius:2px;
font-size:13px;
`;
const Imagee =styled('img')({
  width:'auto',
  height:150

});
const TextT= styled(Typography)`
font-size:14px;
margin-top:5px;

`


const Slide =({products,title,timer})=>{
const timerURL = 'https://static-assets-web.flixcart.com/www/linchpin/fk-cp-zion/img/timer_a73398.svg';
const renderer = ({hours, minutes , seconds})=>{
  return <Box variant="span">{hours}:{minutes}:{seconds} Left</Box>;
}

 return(
  <Component>
   <Deal>
     <DealText>{title}</DealText>
     {
      timer &&
       <Timer>
         <img  src={timerURL} alt="timer" style={{width:24}}/>
         <Countdown date={Date.now()+5.04e+7} renderer={renderer}/>
       </Timer>
     }
     
     <ViewAllButton variant='contained' color='primary'>view All</ViewAllButton>
   </Deal>
   <Divider/>
   <Carousel
     responsive={responsive}
     swipeable={false}
     draggable={false} 
     infinite={true}
     autoPlay={true}
     autoPlaySpeed={3000}
     keyBoardControl={true}
     centerMode={true}
     dotListClass="custom-dot-list-style"
     itemClass="carousel-item-padding-40-px"
     containerClass="carousel-container">
    {  
      products.map((product)=> (
          <Link to={`product/${product.id}`} style={{textDecoration:'none'}}>
             <Box textAlign="center" style={{padding:'25px 15px'}}>
                 <Imagee src={product.url} alt="product" key={product}/>
                 <TextT style={{fontWeight:600, color:"#212121"}}>{product.title.shortTitle}</TextT>
                 <TextT style={{color:'green'}}>{product.discount}</TextT>
                 <TextT style={{color:"#212121",opacity:'.6'}}>{product.tagline}</TextT>
             </Box>
          </Link>  
     ))
    }

   </Carousel>
   </Component>
 )
}
export default Slide;